.loader {
	border: 10px solid #f3f3f3;
	border-radius: 50%;
	border-top: 10px solid green;
	align-content: center;
	width: 50px;
	height: 50px;
	/* -webkit-animation: spin 2s linear infinite; /* Safari */
	animation: spin 2s linear infinite;
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
