body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
	text-align: center;
	display: grid;
	/* height: 100vh; */
	/* width: 100vw; */
	grid-template: 1fr 0.75fr 9fr / 1fr 4fr;
	grid-template-areas:
		'header header'
		'topnav topnav'
		'sidebar main';
}

@media (max-width: 100px) {
	.App {
		font-family: 'Maven Pro', sans-serif;
		background-image: url('/media/background_mobile.png');
		background-repeat: repeat-y;
		background-size: 100%;
		height: 800px;
		-webkit-transition: 250ms;
		transition: 250ms;
		margin-right: -1em;
		margin-left: -1em;
	}
}

.Topnav {
	grid-area: topnav;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: flex;
	        justify-content: flex;
	-webkit-align-items: center;
	        align-items: center;
	border-bottom-color: #282c34;
	font-size: 20px;

	background-color: rgb(191, 209, 186);
}

.Topnav-Button {
	font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
		'Lucida Sans', Arial, sans-serif;
	float: left;
	color: black;
	text-align: center;
	border: 0;
	padding: 30px;
	-webkit-text-decoration: gainsboro;
	        text-decoration: gainsboro;
	font-size: 17px;
	outline: none;
}

.Topnav-Button:hover {
	width: 19%;
	-webkit-transition: 1.5s;
	transition: 1.5s;
	background-color: rgb(106, 132, 99);
	color: white;
}

.Main {
	grid-area: main;
	-webkit-justify-content: center;
	        justify-content: center;
	padding: 1em;
	/* padding-right: 6em; */
	height: 100vh;
	/* width: 100vw; */
	overflow-y: auto;
	overflow: scroll;
}

.Page-Title {
	text-align: center;
}

.Header {
	grid-area: header;
	-webkit-justify-content: center;
	        justify-content: center;
	font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
		'Lucida Sans', Arial, sans-serif;
	font-size: 23px;
	text-align: justify;
	margin-top: 30px;
}

h1 {
	font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
		'Lucida Sans', Arial, sans-serif;
}

.sort-buttons {
	padding: 15px;
}

.sort-buttons:active {
	color: rgb(191, 209, 186);
}
.item-container {
	grid-area: main;
	width: 100%;
	height: 100%;
	-webkit-align-items: left;
	        align-items: left;
	text-align: left;
	display: -webkit-flex;
	display: flex;
}
.individual-item {
	/* border-style: solid; */
	width: 70%;
	text-align: left;
	-webkit-justify-content: left;
	        justify-content: left;
	margin: 20px;
	padding: 2px;
	font-size: 20px;
	border-style: groove;
	border-radius: 10px;
	background-color: white;
}
.individual-item:hover {
	-webkit-transition: 0.25s;
	transition: 0.25s;
	padding: 2px;
	width: 75%;
	background-color: darkcyan;
	border-style: groove;
	border-radius: 10px;
}

.comment-button {
	-webkit-align-content: center;
	        align-content: center;
}

.comment-and-votes {
	font-size: 12px;
}

.Banner {
	text-align: left;
	padding-left: 48px;
	grid-area: header;
	font-size: 80px;
	font-family: 'Times New Roman', Times, serif;
	height: 1fr;
	background-color: darkgreen;
}

p {
	margin: 0;
}

.login-form {
	text-align: center;
	margin: 50px;
}

.Link {
	color: black;
	width: 80%;
}

button {
	background-color: white;
	color: black;
	border: 2px solid #4caf50; /* Green */
	margin: 2px;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.article {
	margin: 10px;
	text-align: justify;
	margin-top: 30px;
}

.Comments-containter {
	display: grid;
	grid-template-rows: 50px;
	grid-template-areas: 'comment';
}

.Comments-item {
	grid-area: comment;
	text-align: left;
	font-size: 14px;
	margin: 10px;
	/* border-bottom-width: 5px; */
	border-bottom: 5px black;
}
.page-title {
	-webkit-align-content: center;
	        align-content: center;
	display: -webkit-flex;
	display: flex;
}

li {
	list-style: none;
}

.comment-form {
	margin-top: 10px;
	-webkit-align-content: center;
	        align-content: center;
}

.comment-box {
	-webkit-align-content: center;
	        align-content: center;
	width: 500px;
	height: 50px;
}

.Comments-item {
	background-color: white;
	border-bottom-width: 1px;
	border-bottom-style: solid;
}

.comment-body {
	font-size: 17px;
}

.comment-delete {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: right;
	        justify-content: right;
}

.Sidebar {
	grid-area: sidebar;
	-webkit-justify-content: flex;
	        justify-content: flex;
	-webkit-align-items: top;
	        align-items: top;
	/* grid-template: 1fr 1fr 1fr 1fr; */
	border-right-width: 6px;
	border-right-style: groove;
	border-right-color: green;
	background-color: lightseagreen;
}

.SideBar-items {
	background-color: aliceblue;
}

.sidebar-text {
	width: 80%;
	height: 60px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: auto;
	        flex-wrap: auto;
}
.topic-table {
	width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: auto;
	        flex-wrap: auto;
	-webkit-align-self: center;
	        align-self: center;
}
.table-row-header {
	text-align: center;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-content: center;
	        align-content: center;
	font-weight: bold;
	border: 1px solid #dddddd;
	text-align: left;
	padding: 8px;
	width: 100%;
}
.table-row {
	text-align: center;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-content: center;
	        align-content: center;
	border: 1px solid #dddddd;
	text-align: left;
	padding: 8px;
	width: 100%;
}

.table-row:hover {
	background-color: gray;
}

.border {
	border-bottom-style: double;
	border-bottom-width: 1px;
}

.write-article-link {
	font-size: 20px;
	display: -webkit-flex;
	display: flex;
}

.welcome-notice {
	-webkit-justify-content: center;
	        justify-content: center;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: auto;
	        flex-wrap: auto;
	width: 50%;
	margin-bottom: 10px;
}

/* tr:nth-child(even) {
	background-color: #dddddd;
} */

.Topic-Link {
	color: rgb(106, 132, 99);
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	font-size: 30px;
	width: 80%;
	-webkit-transition: 1s;
	transition: 1s;
	border-radius: 10px;
	margin-bottom: 20px;
}

/* the :hover that causes the background-color to change */
.Topic-Link:hover {
	background-color: rgb(191, 209, 186);
	border-radius: 10px;
	width: 100%;
}

.User-List {
	padding-top: 20px;
	-webkit-justify-content: center;
	        justify-content: center;
	width: 100%;
	display: grid;
	grid-area: auto;
	/* grid-template: 10em 10em 10em /10em 20em 20em; */
	overflow: scroll;
	height: 100vh;
}

.User-Link {
	width: 100%;
	height: 100%;
	padding: 50px;
	-webkit-justify-content: center;
	        justify-content: center;
	border-color: black;
	color: rgb(106, 132, 99);
	font-size: 30px;
	overflow: scroll;
}

.User-Link:hover {
	background-color: rgb(191, 209, 186);
	border-radius: 10px;
	box-shadow: red;
}

.loader {
	border: 10px solid #f3f3f3;
	border-radius: 50%;
	border-top: 10px solid green;
	-webkit-align-content: center;
	        align-content: center;
	width: 50px;
	height: 50px;
	/* -webkit-animation: spin 2s linear infinite; /* Safari */
	-webkit-animation: spin 2s linear infinite;
	        animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes spin {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

